<template>
  <div id="cropper">
    <VueCropper
      ref="cropper"
      :img="url"
      :info="option.info"
      :outputSize="option.outputSize"
      :outputType="option.outputType"
      :canMove="option.canMove"
      :autoCrop="option.autoCrop"
      :autoCropWidth="option.autoCropWidth"
      :autoCropHeight="option.autoCropHeight"
      :fixedBox="option.fixedBox"
      :original="option.original"
      :infoTrue="option.infoTrue"
      :centerBox="option.centerBox"
      :canMoveBox="option.canMoveBox"
      :canScale="option.canScale"
      :fixed="option.fixed"
      :fixedNumber="option.fixedNumber"
    ></VueCropper>
    <el-button type="primary" @click="startCrop"></el-button>
    <el-button type="primary" @click="getData"></el-button>
  </div>
</template>

<script>
import { VueCropper } from "vue-cropper";
export default {
  components: { VueCropper },
  data() {
    return {
      option: {
        outputSize: 1, //  0.1 - 1
        outputType: "jpg", //	 jpeg || png || webp
        canScale: true, //  true
        autoCrop: false, //  false
        canMove: false, // true
        // autoCropWidth: 0, //	80%	0~max
        // autoCropHeight: 0, //	80%	0~max
        fixedBox: false, //  	false	true | false
        original: false, // 	false	true | false
        infoTrue: true, //  false 	false	true | false
        centerBox: true, // 	false	true | false
        canMoveBox: true, //	true	true | false
        fixed: false ////
        // fixedNumber: [750, 256] //  [, ]
      },
      url: "http://192.168.1.174/file/2225/1.jpg",
      cropperSrc: "",
      param: {
        // 
        originalFilename: "",
        contentType: "image/png",
        base64: "" // data:image/png;base64,
      },
      flag: true,
      percentage: 0 // 
    };
  },
  methods: {
    getData() {
      this.$refs.cropper.getCropData(data => {
        console.log(data);
        console.log(data.replace("data:image/png;base64,", ""));
      });
      const data = this.$refs.cropper.getCropAxis();
      console.log(data);
      setTimeout(() => {
        this.$refs.cropper.clearCrop();
        this.$refs.cropper.stopCrop();
      },1000);
    },
    startCrop() {
      this.$refs.cropper.startCrop();
    }
  }
};
</script>
<style scoped lang="less">
#cropper {
  margin: 0 auto;
  height: 300px;
  width: 300px;
}
</style>

